
import { useState, useEffect, useRef } from '@wordpress/element';
// Settings Components
import Settings from './Settings';
import Style from './Style';
import Feeds from './Feeds';
import { tabController } from './Const/functions';
import BPLSDK from '../bplugins_sdk/src/components/v1/BPLSDK';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;

	useEffect(() => { clientId && '' === attributes.cId && setAttributes({ cId: clientId.substring(0, 8) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [activeIndex, setActiveIndex] = useState(0);

	useEffect(() => {
		setAttributes({ authorized: !!ttpData.tiktokAuthorized });
	}, []);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />

		<BPLSDK setAttributes={setAttributes} />

		<div className={className} id={`ttpTiktok-${attributes.cId}`}>
			<Style attributes={attributes} clientId={attributes.cId} />

			<Feeds attributes={attributes} isBackend={true} react={{ useState, useEffect, useRef }} />
		</div>
	</>;
};
export default Edit;



