import { __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, TextControl, ToggleControl, SelectControl, RangeControl, Button, __experimentalBoxControl as BoxControl, __experimentalUnitControl as UnitControl, Spinner } from '@wordpress/components';

import { produce } from 'immer';
import patterns from './patterns.json';

import { Label, BColor, BDevice, ColorsControl, Typography, HelpPanel } from '../../Components';

import ProModal from '../../my-social-feeds/src/Pro-modal/ProModal';

import { tabController, generateString } from './Const/functions';
import { btnTiktok, comment, heart, playCount } from '../src/Const/icons';
import options from './Const/options';
import { BControlPro } from '../bplugins_sdk/src/components/v1';
import getTimeFromString from './Const/getTimeFromString';
import { parse } from '@wordpress/blocks';
const { profileStyle, generalStyleTabs, pxUnit, perUnit, emUnit, dUnit } = options;

const Settings = ({ attributes, setAttributes, clientId }) => {

	const { isProfile, cId, isVideos, videosLists, profilebgColor, globalPColor, profileAlignment, profilePadding, profilebgCTop, profilebgCbottom, displayNameColor, nameTypo, shareBtn, btnPadding, shareBtnTypo, sharebtnColors, sharebtnHoverColors, layoutColors, pLayoutStyle, feedLayoutStyle, videoCoverImage, countNumColor, borderColor, bioColor, bioTypo, TextColor, InfoTypo, isLightbox, isLike, isComment, isView, isProfileImg, isDisplayName, isShareBtn, isFollowing, isFollowers, isTotalLike, isBio, icon, overlyColor, overlyIconColor, overlyIcon, isBtn, btnTypo, viewLoadBtnIcon, viewLoadBtnPadding, loadMoreBtnColors, loadMoreBtnHoverColors, loadMoreBtn, isPopupContent, columns, columnGap, rowGap, authorized, clearCache, isPremium, profileCacheT, videoCacheT, videosInfo } = attributes;

	const [device, setDevice] = useState('desktop');
	const [feedDevice, setFeedDevice] = useState('desktop');
	const [loading, setLoading] = useState(false);
	const [proModalOpen, setProModalOpen] = useState(false);
	const [postId, setPostId] = useState(null);
	const state = generateString(15);
	// Cache Clear 
	const handleCacheClear = async () => {
		setLoading(true);

		const response = await fetch(`${ttpData.ajaxUrl}?action=ttp_tiktok_clear&nonce=${ttpData?.nonce}&action_type=clear_cache&key=${cId}&profileCacheTime=${getTimeFromString(profileCacheT)}&videoCacheTime=${getTimeFromString(videoCacheT)}`);

		await response.json();
		setLoading(false);
		setAttributes({ clearCache: !clearCache });
	}

	// Authorized 
	const handleUnauthorized = async () => {
		setLoading(true);
		try {
			const response = await fetch(`${ttpData.ajaxUrl}?action=ttp_tiktok_clear&nonce=${ttpData?.nonce}&action_type=unauthorized&key=${cId}`);
			await response.json();
			setAttributes({ authorized: false });
			setLoading(false);
		} catch (error) {
			setLoading(false)
		}
	}

	useEffect(() => {
		if (window.location.href.includes('data')) {
			// wp.data.dispatch('core/editor').autosave();
		}
		const postId = wp.data.select("core/editor").getCurrentPostId();
		setPostId(postId);
	}, []);

	useEffect(() => {
		handleCacheClear();
	}, [videosLists])

	useEffect(() => {
		playCount(overlyIconColor, overlyIcon?.size);
		comment(overlyIconColor, overlyIcon?.size);
		heart(overlyIconColor, overlyIcon?.size);
	}, [overlyIconColor, overlyIcon]);

	useEffect(() => {
		btnTiktok(loadMoreBtnColors?.color, viewLoadBtnIcon?.size);
	}, [viewLoadBtnIcon]);

	return <>
		<InspectorControls>
			{authorized ?
				<div className='ttpAuthorization'>
					<Button className='ttpAuthBtn' onClick={handleUnauthorized} disabled={loading}>{__('Remove Authorize', 'tiktok')}</Button>
					<Button className='ttpAuthBtn' disabled={loading} onClick={handleCacheClear}>{__('Clear Cache', 'tiktok')}</Button>

					{loading && <h3 className='wppLoading'><Spinner /></h3>}
				</div>
				: <div className='ttpAuthorization'><Button className='ttpAuthBtn' onClick={async () => {
					const { href, origin, pathname } = window.location;
					const beforeWPAdmin = pathname?.substring(0, pathname?.indexOf('wp-admin'));

					const pageUrl = origin?.includes('action=edit') ? href : `${origin}${beforeWPAdmin}wp-admin/post.php?post=${postId}&action=edit`;

					// const pageUrl = window.location.origin?.includes('action=edit') ? window.location.href : `${window.location.origin}${window.location.pathname}?post=${postId}&action=edit`;
					// const pageUrl = window.location.origin?.includes('action=edit') ? window.location.href : `${window.location.origin}/wp-admin/post.php?post=${postId}&action=edit`
					await wp.data.dispatch('core/editor').savePost();
					window.location.href = `https://api.bplugins.com/tiktok-landing/?state=${state}&redirect_url=${pageUrl}`
				}} > {__('Add TikTok Account', 'tiktok')}</Button></div>
			}

			{authorized && <TabPanel className='bPlTabPanel ttpTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					<HelpPanel slug="b-tiktok-feed" docsLink="https://wptiktokfeed.com/docs" />

					<PanelBody className='bPlPanelBody ttpPanelBody' title={__('Profile', 'tiktok')} initialOpen={false}>
						{/* Profile  */}
						<ToggleControl className={`mt20`} label={__('Profile', 'tiktok')} checked={isProfile}
							onChange={(val) => setAttributes({ isProfile: val })} />

						{isProfile && <>
							<ToggleControl label={__('Profile Image', 'tiktok')} className='mt20' checked={isProfileImg}
								onChange={(val) => { setAttributes({ isProfileImg: val }); }}
							/>
							<ToggleControl label={__('Display Name', 'tiktok')} className='mt20' checked={isDisplayName}
								onChange={(val) => { setAttributes({ isDisplayName: val }); }} />

							<ToggleControl className='mt20' label={__('Share button', 'tiktok')} checked={isShareBtn} onChange={(val) => { setAttributes({ isShareBtn: val }); }} />

							<ToggleControl label={__('Following', 'tiktok')} className='mt20' checked={isFollowing}
								onChange={(val) => { setAttributes({ isFollowing: val }); }} />

							<ToggleControl label={__('Followers', 'tiktok')} className='mt20' checked={isFollowers}
								onChange={(val) => { setAttributes({ isFollowers: val }); }} />

							<ToggleControl label={__('Total Like', 'tiktok')} className='mt20' checked={isTotalLike} onChange={(val) => { setAttributes({ isTotalLike: val }); }} />

							<ToggleControl label={__('Biography', 'tiktok')} className='mt20' checked={isBio}
								onChange={(val) => { setAttributes({ isBio: val }); }} />
						</>}
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Content/Videos', 'tiktok')} initialOpen={false}>
						<BControlPro className={`mt20`} label={__('Videos', 'tiktok')} checked={isVideos} onChange={(val) => setAttributes({ isVideos: val })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro label={__('View on TikTok', 'tiktok')} className='mt20' checked={isBtn?.viewOn} onChange={(val) => { setAttributes({ isBtn: { ...isBtn, viewOn: val } }); }} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro label={__('Load More', 'tiktok')} className='mt20' checked={isBtn?.loadMore} onChange={(val) => { setAttributes({ isBtn: { ...isBtn, loadMore: val } }); }} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Details in popup', 'tiktok')} checked={isPopupContent?.rightContent} onChange={(val) => setAttributes({ isPopupContent: { ...isPopupContent, rightContent: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Title', 'tiktok')} checked={videosInfo?.title} onChange={(val) => setAttributes({ videosInfo: { ...videosInfo, title: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Comment', 'tiktok')} checked={videosInfo?.comment} onChange={(val) => setAttributes({ videosInfo: { ...videosInfo, comment: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Like', 'tiktok')} checked={videosInfo?.like} onChange={(val) => setAttributes({ videosInfo: { ...videosInfo, like: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Date', 'tiktok')} checked={videosInfo?.date} onChange={(val) => setAttributes({ videosInfo: { ...videosInfo, date: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

						<BControlPro className={`mt20`} label={__('Line', 'tiktok')} checked={videosInfo?.line} onChange={(val) => setAttributes({ videosInfo: { ...videosInfo, line: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />

					</PanelBody>

					{isVideos && <>
						<PanelBody className='bPlPanelBody' title={__('Layout', 'tiktok')} initialOpen={false}>

							<BControlPro label={__('Layout', 'tiktok')} labelPosition="side" value={feedLayoutStyle} options={options?.feedLayoutOpt} onChange={(val) => {
								setAttributes({ feedLayoutStyle: val })
							}} isPremium={isPremium} setOpen={setProModalOpen} Component={SelectControl} />

							<PanelRow className='mt20'>
								<Label mt='0'>{__('Feed Per Page', 'tiktok')}</Label>
								<BControlPro device={feedDevice} onChange={val => setFeedDevice(val)}
									isPremium={isPremium} setOpen={setProModalOpen}
									Component={BDevice} />
							</PanelRow>

							<BControlPro value={videosLists[feedDevice]} onChange={val =>
								setAttributes({ videosLists: { ...videosLists, [feedDevice]: val } })}
								isPremium={isPremium} setOpen={setProModalOpen} Component={RangeControl}
								min={1} max={20} step={1} />

							{/* Check Slider Layout  */}
							{feedLayoutStyle !== "slider" && <>
								{/* column Gap  */}
								<UnitControl className='mt20' label={__('Column Gap:', 'tiktok')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(30), perUnit(3), emUnit(2)]} isResetValueOnUnitChange={true} />


								{/* row Gap  */}
								<UnitControl className='mt20' label={__('Row Gap:', 'tiktok')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(40), perUnit(3), emUnit(2.5)]} isResetValueOnUnitChange={true} />


								{/* column define option  */}
								<PanelRow className='mt20'>
									<Label mt='0'>{__('Columns:', 'tiktok')}</Label>
									<BDevice device={device} onChange={val => setDevice(val)} />
								</PanelRow>

								<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />
								{feedLayoutStyle === 'default' &&
									<BControlPro label={__(' Lightbox ', 'tiktok')} className='mt20' checked={isLightbox} onChange={(val) => { setAttributes({ isLightbox: val }); }} isPremium={isPremium} setOpen={setProModalOpen} Component={ToggleControl} />
								}
							</>}
						</PanelBody>

						<PanelBody className='bPlPanelBody ttpPanelBody' title={__('Video Overly', 'tiktok')} initialOpen={false}>
							<BControlPro label={__('Show Like ', 'tiktok')} className='mt20' checked={isLike}
								onChange={(val) => { setAttributes({ isLike: val }); }} isPremium={isPremium}
								setOpen={setProModalOpen} Component={ToggleControl} />
							<BControlPro label={__('Show Comment ', 'tiktok')} className='mt20' checked={isComment}
								onChange={(val) => { setAttributes({ isComment: val }); }} isPremium={isPremium}
								setOpen={setProModalOpen} Component={ToggleControl} />
							<BControlPro label={__('Show View ', 'tiktok')} className='mt20' checked={isView}
								onChange={(val) => { setAttributes({ isView: val }); }} isPremium={isPremium}
								setOpen={setProModalOpen} Component={ToggleControl} />

						</PanelBody>
					</>}

					{(isVideos && (feedLayoutStyle === 'default' || feedLayoutStyle === 'slider')) &&
						<PanelBody className='bPlPanelBody' title={__('Video Image Aspect Ratio', 'tiktok')} initialOpen={false}>
							<BControlPro className="mt15" label={__('Ratio', 'tiktok')} labelPosition="side" value={videoCoverImage?.ratio} options={options?.ratioOpt} onChange={(val) => {
								setAttributes({ videoCoverImage: { ...videoCoverImage, ratio: val } })
							}} isPremium={isPremium} setOpen={setProModalOpen} Component={SelectControl} />
						</PanelBody>}

					<PanelBody className='bPlPanelBody' title={__('Cache Time', 'tiktok')} initialOpen={false}>
						<div className="ttpCacheTime">
							{isProfile && <BControlPro className='mt20' label={__('Profile:', 'tiktok')}
								labelPosition='left' value={profileCacheT} onChange={profileCacheT => {
									setAttributes({ profileCacheT }); handleCacheClear();
								}} units={[dUnit(60, 'px', 'Minutes'), dUnit(1, 'em', 'Hours')]} isResetValueOnUnitChange={true} Component={UnitControl} isPremium={isPremium} setOpen={setProModalOpen}
							/>}

							{isVideos && <BControlPro className='mt20' label={__('Video:', 'tiktok')} labelPosition='left' value={videoCacheT} onChange={videoCacheT => { setAttributes({ videoCacheT }); handleCacheClear(); }} units={[dUnit(60, 'px', 'Minutes'), dUnit(1, 'em', 'Hours')]} isResetValueOnUnitChange={true} Component={UnitControl} isPremium={isPremium} setOpen={setProModalOpen}
							/>}
						</div>

					</PanelBody>


				</>}

				{'style' === tab.name && <>
					{isProfile && <>
						<PanelBody className='bPlPanelBody' title={__('Profile', 'tiktok')} initialOpen={false}>

							{/* Profile Layout Select  */}
							<BControlPro isPremium={isPremium} setOpen={setProModalOpen} Component={SelectControl} className='mt15' labelPosition='side' label={__('Layout', 'tiktok')} value={pLayoutStyle}
								onChange={(val) => {
									setAttributes({ pLayoutStyle: val })
									if ('compact' === val) {
										setAttributes({
											TextColor: "#5c5757",
											countNumColor: "#000",
											btnPadding: { "top": "2px", "right": "6px", "bottom": "2px", "left": "6px" },
											shareBtnTypo: {
												...shareBtnTypo,
												fontSize: 12,
												fontWeight: 600,
												lineHeight: "135%"
											},
											nameTypo: {
												...nameTypo,
												fontSize: 18,
												fontWeight: 500
											}
										})
									}
									if (['default', 'card'].includes(val)) {
										setAttributes({
											countNumColor: "#ffffffe6",
											TextColor: "#ffffffbf",
											btnPadding: { "top": "4px", "right": "8px", "bottom": "4px", "left": "8px" },
											shareBtnTypo: {
												...shareBtnTypo,
												fontSize: 18,
												fontWeight: 600
											},
											nameTypo: {
												...nameTypo,
												fontSize: 32,
												fontWeight: 700
											}
										})
									}

								}} options={profileStyle}
								__nextHasNoMarginBottom />

							{/* profile Alignment  */}
							<SelectControl className='mt15 mb15' labelPosition='side' label={__('Alignment', 'tiktok')} value={profileAlignment}
								onChange={(val) => {

									if ('compact' === pLayoutStyle) {
										if ('flex-start' === val) {
											setAttributes({ profileAlignment: "left" })
										}
										else if ('flex-end' === val) {
											setAttributes({ profileAlignment: "right" })
										}
										else (
											setAttributes({ profileAlignment: "center" })
										)
									}
									else {
										setAttributes({ profileAlignment: val });
									}
								}} options={options.alignSelect}
								__nextHasNoMarginBottom
							/>

							{/* Background Color  */}
							<BColor className="mb10" label={__('Background Color', 'tiktok')} value={profilebgColor} onChange={val => setAttributes({ profilebgColor: val })} defaultColor={{ color: '#000' }} />

							{/* Global Color  */}
							<BColor className="mb10" label={__('Color', 'tiktok')} value={globalPColor}
								onChange={(val) =>
									setAttributes({
										globalPColor: val,
										displayNameColor: val,
										sharebtnColors: { ...sharebtnColors, color: val, styles: `color: ${val}; background: ${sharebtnColors?.bg}` },
										countNumColor: val,
										TextColor: val,
										borderColor: val,
										bioColor: val
									})}
								defaultColor={{ color: '#fff' }} />

							<BoxControl label={__('Padding', 'tiktok')} values={profilePadding} onChange={val => setAttributes({ profilePadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />
						</PanelBody>

						{'compact' === pLayoutStyle && <PanelBody className='bPlPanelBody' title={__('Compact Profile', 'tiktok')} initialOpen={false}>
							{/* profile img and button  */}
							<BColor label={__('Top Background Color:', 'tiktok')} value={profilebgCTop} onChange={val => setAttributes({ profilebgCTop: val })} defaultColor='#333' />
							{/* profileInfo Bottom  */}
							<BColor label={__('Bottom Background Color:', 'tiktok')} value={profilebgCbottom} onChange={val => setAttributes({ profilebgCbottom: val })} defaultColor='#fff' />
						</PanelBody>}

						{isDisplayName &&
							<PanelBody className='bPlPanelBody' title={__('Name', 'tiktok')} initialOpen={false}>

								<BControlPro className='mt20' label={__('Typography', 'titkok')} value={nameTypo} onChange={val => setAttributes({ nameTypo: val })} defaults={{ fontSize: 18 }} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />

								<BControlPro label={__('Color', 'tiktok')} value={displayNameColor} onChange={val => setAttributes({ displayNameColor: val })} defaultColor={{ color: '#fff' }} isPremium={isPremium} Component={BColor} setOpen={setProModalOpen} />
							</PanelBody>}

						{isShareBtn &&
							<PanelBody className='bPlPanelBody sharePanelBody' title={__('Share Button', 'tiktok')} initialOpen={false}>

								<BControlPro label={__('Text', 'tiktok')} className='mt15 shareBtn' value={shareBtn}
									onChange={(val) => setAttributes({ shareBtn: val })} Component={TextControl} isPremium={isPremium} setOpen={setProModalOpen}
								/>
								<BControlPro className='mt20 mb20' label={__('Typography:', 'titkok')} value={shareBtnTypo} onChange={val => setAttributes({ shareBtnTypo: val })} defaults={{ fontSize: 12 }} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />

								<BoxControl label={__('Padding', 'tiktok')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })} units={[pxUnit(3), emUnit(2)]} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} />

								<ColorsControl className='' label={__('Colors', 'tiktok')} value={sharebtnColors} onChange={val => setAttributes({ sharebtnColors: val })} defaults={{ color: '#fff', bg: '#ff3b5c' }} />

								<ColorsControl className='' label={__('Hover Colors', 'tiktok')} value={sharebtnHoverColors} onChange={val => setAttributes({ sharebtnHoverColors: val })} defaults={{ color: '#fff', bg: '#fe2c55' }} />

							</PanelBody>}

						{(isFollowing || isFollowers || isTotalLike) &&
							<PanelBody className='bPlPanelBody' title={__('Info Count', 'tiktok')} initialOpen={false}>
								<BControlPro label={__('Count Color', 'tiktok')} value={countNumColor} onChange={val => setAttributes({ countNumColor: val })} defaultColor='#fff' isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />

								<BControlPro label={__('Text Color', 'tiktok')} value={TextColor} onChange={val => setAttributes({ TextColor: val })} defaultColor='#ffffffbf' isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />

								<BControlPro className='mt20' label={__('Typography:', 'titkok')} value={InfoTypo} onChange={val => setAttributes({ InfoTypo: val })} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />

								{['card', 'compact'].includes(pLayoutStyle) &&
									<BControlPro label={__('Border Color', 'tiktok')} value={borderColor} onChange={val => setAttributes({ borderColor: val })} defaultColor='#000' isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />
								}
							</PanelBody>}

						{['default', 'card'].includes(pLayoutStyle) && <>
							{isBio &&
								<PanelBody className='bPlPanelBody' title={__('Biography', 'tiktok')} initialOpen={false}>
									<BControlPro label={__('Color', 'tiktok')} value={bioColor} onChange={val => setAttributes({ bioColor: val })} defaultColor='#fff' isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />

									<BControlPro className='mt20' label={__('Typography', 'business-review')} value={bioTypo} onChange={val => setAttributes({ bioTypo: val })} defaults={{ fontSize: 17 }} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />
								</PanelBody>
							}
						</>}
					</>}

					{isVideos && <PanelBody className='bPlPanelBody sharePanelBody' title={__('Gallery', 'tiktok')} initialOpen={false}>
						<ColorsControl label={__('Colors', 'tiktok')} value={layoutColors} onChange={(val) => {
							setAttributes({ layoutColors: val })
						}} />
					</PanelBody>}

					{/* profile Condition End */}
					{isVideos && <>
						<PanelBody className='bPlPanelBody sharePanelBody' title={__('Load More', 'tiktok')} initialOpen={false}>

							<BControlPro label={__('Text', 'tiktok')} className='mt15 shareBtn' value={loadMoreBtn}
								onChange={(val) => setAttributes({ loadMoreBtn: val })} Component={TextControl} isPremium={isPremium} setOpen={setProModalOpen} />

						</PanelBody>

						<PanelBody className='bPlPanelBody' title={__('Overly', 'tiktok')} initialOpen={false}>

							<BControlPro className="mt10 mb10" label={__('Color', 'tiktok')} value={overlyColor} onChange={val => setAttributes({ overlyColor: val })} isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />

							<BControlPro className="mt10 mb10" label={__('Icon Color', 'tiktok')} value={overlyIconColor} onChange={val => setAttributes({ overlyIconColor: val })} defaultColor='#fff' isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} />

							<RangeControl label={__('Icon size', 'tiktok')} value={overlyIcon?.size} onChange={(val) => { setAttributes({ overlyIcon: { ...overlyIcon, size: val } }) }} min={1} max={100} />

						</PanelBody>

						<PanelBody className='bPlPanelBody' title={__('Icon', 'tiktok')} initialOpen={false}>

							{/* <BControlPro className="mt10 mb10" label={__('Color', 'tiktok')} value={icon?.color} onChange={val => setAttributes({ icon: { ...icon, color: val } })} isPremium={isPremium} setOpen={setProModalOpen} Component={BColor} /> */}

							<BControlPro value={icon?.size} onChange={(val) => { setAttributes({ icon: { ...icon, size: val } }) }} min={1} max={100} isPremium={isPremium} setOpen={setProModalOpen} Component={RangeControl} />
						</PanelBody>

						<PanelBody className='bPlPanelBody' title={__('Button', 'tiktok')} initialOpen={false}>

							<BControlPro className='mt20' label={__('Typography', 'titkok')} value={btnTypo} onChange={val => setAttributes({ btnTypo: val })} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />

							<ColorsControl className='' label={__('Colors', 'tiktok')} value={loadMoreBtnColors} onChange={val => setAttributes({ loadMoreBtnColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

							<ColorsControl className='' label={__('Hover Colors', 'tiktok')} value={loadMoreBtnHoverColors} onChange={val => setAttributes({ loadMoreBtnHoverColors: val })} defaults={{ color: '#000', bg: '#fff' }} />

							<BControlPro label={__('Icon Size', 'tiktok')} value={viewLoadBtnIcon?.size} onChange={(val) => { setAttributes({ viewLoadBtnIcon: { ...viewLoadBtnIcon, size: val } }) }} min={1} max={100} isPremium={isPremium} Component={RangeControl} setOpen={setProModalOpen} />

							<BoxControl label={__('Padding', 'clipboard')} values={viewLoadBtnPadding} onChange={val => setAttributes({ viewLoadBtnPadding: val })} resetValues={{ top: "6px", right: "12px", bottom: "6px", left: "12px" }} units={[pxUnit(3), emUnit(2)]} />

						</PanelBody>
					</>}
				</>}


				{'patterns' === tab.name && <>
					{!isPremium && <p className='noticePatter'>{__('it\'s only available on pro version', 'Tiktok')}</p>}
					{patterns.map((pattern, index) => {
						const imgName = pattern.name.replace('/', '-');
						const imgPath = ttpPatters.patternsImagePath + imgName + '.png';
						return <div key={index} className='bsbPatters'>
							<img key={index} style={{ width: '100%' }} src={imgPath} />
							<div className="overlyButton">
								<button onClick={() => {
									if (isPremium) {
										const blocks = parse(pattern.content);
										wp.data.dispatch('core/block-editor').replaceBlock(clientId, blocks);
									} else {
										setProModalOpen(true);
									}
								}}>{__('Apply', 'slider')}</button>
							</div>
						</div>
					})}
				</>}
			</>}</TabPanel>}
		</InspectorControls>
		{/* Moadal  */}
		<ProModal isProModal={proModalOpen} setIsProModal={setProModalOpen} block='B TikTok Feeds'> 
			<li>{__('Videos per page', 'tiktok-feed')}</li>
			<li>{__('Show Hide Video Overly like,share and view', 'tiktok-feed')}</li>
			<li>{__('Video overly icon style', 'tiktok-feed')}</li>
			<li>{__('Share button text change', 'tiktok-feed')}</li>
			<li>{__('Share button style', 'tiktok-feed')}</li>
			<li>{__('Cache time set profile and video', 'tiktok-feed')}</li>
			<li>{__('Profile 3 layout', 'tiktok-feed')}</li> 
			<li>{__('Profile name style', 'tiktok-feed')}</li>
			<li>{__('Info style', 'tiktok-feed')}</li>
			<li>{__('Load more button text change', 'tiktok-feed')}</li> 
		</ProModal>

	</>;
};
export default Settings;